/**
 * 
 */
export const uploadFile = async (file) => {
  try {
    const formData = new FormData()
    formData.append('file', file)

    const baseURL = await getBackendURL()
    const response = await axiosInstance.post(`${baseURL}/api/Files/upload`, formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
    
    // API
    return response.data
  } catch (error) {
    return {
      success: false,
      data: null,
      message: error.response?.data?.message || ''
    }
  }
}

/**
 * 
 */
export const uploadFiles = async (files) => {
  try {
    const formData = new FormData()
    files.forEach(file => {
      formData.append('files', file)
    })
    
    const baseURL = await getBackendURL()
    const response = await axiosInstance.post(`${baseURL}/api/Files/upload-multiple`, formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
    
    // API
    return response.data
  } catch (error) {
    return {
      success: false,
      data: null,
      message: error.response?.data?.message || ''
    }
  }
}

/**
 * URL
 */
export const getMediaUrl = (id, fileName, thumbnail) => {
  if(!thumbnail)
    thumbnail = false
  const baseURL = config.API_BASE_URL
  return `${baseURL}/api/Files/download?id=${encodeURIComponent(id)}&fileName=${encodeURIComponent(fileName)}&thumbnail=${thumbnail}`
}

export const getMediaUrlNew = async (id, fileName, thumbnail) => {
  if(!thumbnail)
    thumbnail = false
  const baseURL = await getBackendURL()
  return `${baseURL}/api/Files/download?id=${encodeURIComponent(id)}&fileName=${encodeURIComponent(fileName)}&thumbnail=${thumbnail}`
}

/**
 * 
 */
export const getStats = async () => {
  try {
    // 
    // const response = await apiClient.get('/api/Stats')
    // return { success: true, data: response.data, message: '' }
    
    // 
    const eventsResponse = await getEventsList()
    if (!eventsResponse.success) {
      return {
        success: false,
        data: null,
        message: ''
      }
    }
    
    const allEvents = eventsResponse.data.flatMap(group => group.events)
    const totalEvents = allEvents.length
    const totalPhotos = allEvents.reduce((total, event) => 
      total + (event.media?.images?.length || 0), 0)
    const totalVideos = allEvents.reduce((total, event) => 
      total + (event.media?.videos?.length || 0), 0)
    const totalAudios = allEvents.reduce((total, event) => 
      total + (event.media?.audios?.length || 0), 0)
    
    return {
      success: true,
      data: {
        totalEvents,
        totalPhotos,
        totalVideos,
        totalAudios
      },
      message: ''
    }
  } catch (error) {
    return {
      success: false,
      data: null,
      message: ''
    }
  }
}


/**
 * 
 * @param {Object} request - 
 * C#
 * public class InitChunkUploadRequest
 * {
 *   public string FileName { get; set; }
 *   public string FileType { get; set; }
 *   public long FileSize { get; set; }
 *   public int ChunkCount { get; set; }
 *   public string FileMD5 { get; set; }                  // MD5
 * }
     * @returns {Promise<Object>} - API
     * C#
 * public class InitChunkUploadResponse
 * {
 *   public string TaskId { get; set; }
 * }
 */
export const initChunk = async (request) => {
  try {
    const baseURL = await getBackendURL()
    const response = await axiosInstance.post(`${baseURL}/api/Chunk/init`, request)
    // API
    return response.data
  } catch (error) {
    return {
      success: false,
      data: null,
      message: error.response?.data?.message || ''
    }
  }
}

/** 
 * @param {string} taskId - ID
 * @param {number} chunkIndex - 
 * @param {File} file - 
 * @returns {Promise<Object>} - API
 * C#
 * public class UploadChunkResponse
 * {
 *   public int ChunkIndex { get; set; }
 *   public int CompletedChunks { get; set; }
 *   public int TotalChunks { get; set; }
 * }
 */
export const uploadChunk = async (taskId, chunkIndex, file) => {
  try {
    const formData = new FormData()
    formData.append('file', file)

    const baseURL = await getBackendURL()
    const response = await axiosInstance.post(`${baseURL}/api/Chunk/upload?taskId=${encodeURIComponent(taskId)}&chunkIndex=${chunkIndex}`, formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
    // API
    return response.data
  } catch (error) {
    return {
      success: false,
      data: null,
      message: error.response?.data?.message || ''
    }
  }
}

