<template>
  <div class="chunk-uploader">
    <!--  -->
    <div class="upload-dialog">
      <div class="upload-dialog-header">
        <h3></h3>
        <button 
          type="button" 
          class="toggle-logs-btn" 
          @click="showLogs = !showLogs"
        >
          {{ showLogs ? '' : '' }}
        </button>
      </div>
      <div class="upload-dialog-body">
        <div v-for="(file, index) in uploadTasks" :key="index" class="file-item">
          <div class="file-info">
            <div class="file-name">{{ file.name }}</div>
            <div class="file-size">{{ formatFileSize(file.size) }}</div>
            <div class="file-status">
              {{ getFileStatus(file) }}
              <span v-if="file.error" class="error-message">: {{ file.error }}</span>
            </div>
          </div>
          <div class="progress-container">
            <div class="progress-bar" :style="{ width: file.progress + '%' }"></div>
            <div class="progress-text">{{ file.progress.toFixed(1) }}%</div>
          </div>
        </div>

        <!--  -->
        <div v-if="showLogs" class="log-container">
          <div class="log-header">
            <h4></h4>
            <div class="log-actions">
              <button 
                type="button" 
                class="clear-logs-btn" 
                @click="clearLogs"
              >
                
              </button>
              <button
                type="button"
                class="copy-logs-btn"
                @click="copyLogs"
              >
                
              </button>
            </div>
          </div>
          <div class="log-content" ref="logContent">
            <div v-for="(log, index) in logs" :key="index" class="log-entry" :class="getLogClass(log)">
              <span class="log-time">{{ log.time }}</span>
              <span class="log-level">[{{ log.level }}]</span>
              <span class="log-message">{{ log.message }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { initChunk, uploadChunk, completeChunk } from '../api/events';
import SparkMD5 from 'spark-md5';

export default {
  name: 'ChunkUploader',
  props: {
    // 
    maxParallelFiles: {
      type: Number,
      default: 3
    },
    // 
    maxParallelChunks: {
      type: Number,
      default: 3
    },
    // (bytes) -  512k
    chunkSize: {
      type: Number,
      default:  512 * 1024
    }
  },
  data() {
    return {
      uploadTasks: [],
      activeUploads: 0,
      isUploading: false,
      showLogs: false,
      logs: [],
      maxLogs: 100  // 
    };
  },
  computed: {
    allTasksCompleted() {
      return this.uploadTasks.length > 0 && 
             this.uploadTasks.every(task => task.status === 'completed' || task.status === 'error');
    }
  },
  methods: {
    // 
    log(level, message) {
      const time = new Date().toLocaleTimeString();
      this.logs.unshift({ time, level, message });
      
      // 
      if (this.logs.length > this.maxLogs) {
        this.logs = this.logs.slice(0, this.maxLogs);
      }
      
      // 
      switch (level) {
        case 'ERROR':
          console.error(message);
          break;
        case 'WARN':
          console.warn(message);
          break;
        case 'INFO':
          console.log(message);
          break;
        default:
          console.log(message);
      }
      
      // 
      this.$nextTick(() => {
        if (this.showLogs && this.$refs.logContent) {
          this.$refs.logContent.scrollTop = 0;
        }
      });
    },
    
    logInfo(message) {
      this.log('INFO', message);
    },
    
    logWarn(message) {
      this.log('WARN', message);
    },
    
    logError(message) {
      this.log('ERROR', message);
    },
    
    clearLogs() {
      this.logs = [];
    },
    
    copyLogs() {
      // 
      const logText = this.logs
        .map(log => `${log.time} [${log.level}] ${log.message}`)
        .join('\n');
      
      // 
      navigator.clipboard.writeText(logText)
        .then(() => {
          this.logInfo('');
        })
        .catch(err => {
          this.logError(': ' + err);
        });
    },
    
    getLogClass(log) {
      return {
        'log-info': log.level === 'INFO',
        'log-warn': log.level === 'WARN',
        'log-error': log.level === 'ERROR'
      };
    },
    
    // 
    addFile(file) {
      try {
        if (!file) {
          this.logError('!');
          return null;
        }
        
        this.logInfo(`: ${file.name}, : ${this.formatFileSize(file.size)}`);
        
        // 
        const newTask = {
          file,
          name: file.name,
          size: file.size,
          progress: 0,
          status: 'pending', // pending, calculating, uploading, completed, error
          error: null,
          taskId: null,
          totalChunks: Math.ceil(file.size / this.chunkSize),
          uploadedChunks: 0,
          chunkQueue: [],
          md5: null,
          captureTime: null
        };
        
        // 
        this.uploadTasks.push(newTask);
        this.logInfo(`: ${this.uploadTasks.length}`);
        
        // MD5
        if (!this.isUploading) {
          this.logInfo('');
          setTimeout(() => {
            try {
              this.startUpload();
            } catch (error) {
              this.logError(`: ${error.message || error}`);
            }
          }, 100); // DOM
        } else {
          this.logInfo('');
        }
        
        return newTask;
      } catch (error) {
        this.logError(`: ${error.message || error}`);
        return null;
      }
    },
    
    clearCompletedTasks() {
      // 
      this.uploadTasks = this.uploadTasks.filter(
        task => task.status !== 'completed' && task.status !== 'error'
      );
    },
    
    formatFileSize(bytes) {
      if (bytes < 1024) return bytes + ' B';
      else if (bytes < 1024 * 1024) return (bytes / 1024).toFixed(2) + ' KB';
      else if (bytes < 1024 * 1024 * 1024) return (bytes / (1024 * 1024)).toFixed(2) + ' MB';
      else return (bytes / (1024 * 1024 * 1024)).toFixed(2) + ' GB';
    },
    
    getFileStatus(file) {
      switch (file.status) {
        case 'pending': return '';
        case 'calculating': return 'MD5...';
        case 'uploading': return ` (${file.uploadedChunks}/${file.totalChunks})`;
        case 'verifying': return '...';
        case 'completed': return '';
        case 'error': return '';
        default: return file.status;
      }
    },
    
    async startUpload() {
      if (this.isUploading) {
        this.logInfo('');
        return;
      }
      
      this.logInfo(`, : ${this.uploadTasks.length}`);
      this.isUploading = true;
      
      try {
        // 
        const pendingTasks = this.uploadTasks.filter(task => 
          task.status === 'pending' || (task.status === 'error' && !task.taskId)
        );
        
        this.logInfo(`: ${pendingTasks.length}`);
        
        if (pendingTasks.length === 0) {
          this.logInfo('');
          this.isUploading = false;
          // 
          this.checkAllTasksCompleted();
          return;
        }
        
        // MD5
        this.logInfo(` ${pendingTasks.length} MD5`);
        for (const task of pendingTasks) {
          await this.calculateFileMD5(task);
        }
      } catch (error) {
        this.logError(`: ${error.message || error}`);
        this.isUploading = false;
      }
    },
    
    async calculateFileMD5(task) {
      this.logInfo(`MD5: ${task.name}`);
      task.status = 'calculating';
      
      try {
        const md5 = await this.computeFileMD5(task.file);
        this.logInfo(`MD5: ${md5}`);
        task.md5 = md5;
        await this.prepareFileUpload(task);
      } catch (error) {
        this.logError(`MD5: ${error.message || error}`);
        task.status = 'error';
        task.error = 'MD5';
        this.checkAllTasksCompleted();
      }
    },
    
    async computeFileMD5(file) {
      return new Promise((resolve, reject) => {
        this.logInfo(`MD5: ${file.name}, : ${file.size}`);
        
        // 
        if (file.size < 1024 * 1024) { // 1MB
          this.logInfo('MD5');
          const reader = new FileReader();
          reader.onload = (e) => {
            try {
              const arrayBuffer = e.target.result;
              const spark = new SparkMD5.ArrayBuffer();
              spark.append(arrayBuffer);
              const hash = spark.end();
              resolve(hash);
            } catch (err) {
              reject(err);
            }
          };
          reader.onerror = (err) => reject(err);
          reader.readAsArrayBuffer(file);
          return;
        }
        
        // 
        const blobSlice = File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice;
        const chunkSize = 2097152; // 2MB per chunk for MD5 calculation
        const chunks = Math.ceil(file.size / chunkSize);
        let currentChunk = 0;
        const spark = new SparkMD5.ArrayBuffer();
        const fileReader = new FileReader();
        
        this.logInfo(` ${chunks} `);
        
        fileReader.onload = (e) => {
          try {
            spark.append(e.target.result); // Append array buffer
            currentChunk++;
            
            const progress = Math.round((currentChunk / chunks) * 100);
            if (currentChunk % 10 === 0 || currentChunk === chunks) {
              this.logInfo(`MD5: ${progress}%`);
            }
            
            if (currentChunk < chunks) {
              loadNext();
            } else {
              const hash = spark.end();
              this.logInfo(`MD5: ${hash}`);
              resolve(hash); // Return the MD5 hash
            }
          } catch (error) {
            this.logError(`MD5: ${error.message || error}`);
            reject(error);
          }
        };
        
        fileReader.onerror = (error) => {
          this.logError(`FileReader: ${error.message || error}`);
          reject(error);
        };
        
        const loadNext = () => {
          try {
            const start = currentChunk * chunkSize;
            const end = Math.min(file.size, start + chunkSize);
            const chunk = blobSlice.call(file, start, end);
            fileReader.readAsArrayBuffer(chunk);
          } catch (error) {
            this.logError(`: ${error.message || error}`);
            reject(error);
          }
        };
        
        loadNext();
      });
    },
    
    async prepareFileUpload(task) {
      try {
        this.logInfo(`: ${task.name}`);
        
        // 
        const initRequest = {
          fileName: task.name,
          fileType: task.file.type,
          fileSize: task.file.size,
          chunkCount: task.totalChunks,
          fileMD5: task.md5
        };
        
        this.logInfo(`: ${JSON.stringify(initRequest)}`);
        
        // 
        const response = await initChunk(initRequest);
        this.logInfo(`: ${JSON.stringify(response)}`);
        
        if (response.success && response.data) {
          task.taskId = response.data.taskId;
          task.status = 'uploading';
          this.logInfo(`ID: ${task.taskId}, `);
          
          this.createChunkQueue(task);
          this.processUploadQueue();
        } else {
          this.logError(`: ${JSON.stringify(response)}`);
          task.status = 'error';
          task.error = response.message || '';
          this.checkAllTasksCompleted();
        }
      } catch (error) {
        this.logError(`: ${error.message || error}`);
        task.status = 'error';
        task.error = '';
        this.checkAllTasksCompleted();
      }
    },
    
    createChunkQueue(task) {
      const { file } = task;
      const { chunkSize } = this;
      task.chunkQueue = [];
      
      for (let i = 0; i < task.totalChunks; i++) {
        const start = i * chunkSize;
        const end = Math.min(file.size, start + chunkSize);
        const chunkFile = file.slice(start, end);
        
        task.chunkQueue.push({
          index: i,
          file: chunkFile,
          uploaded: false,
          uploading: false,
          retries: 0
        });
      }
    },
    
    async processUploadQueue() {
      this.logInfo('');
      
      // 
      this.activeUploads = this.uploadTasks.filter(task => 
        task.status === 'uploading' && task.chunkQueue.some(chunk => chunk.uploading)
      ).length;
      
      this.logInfo(`: ${this.activeUploads}, : ${this.maxParallelFiles}`);
      
      // 
      for (const task of this.uploadTasks) {
        if (task.status === 'uploading' && !task.chunkQueue.some(chunk => chunk.uploading)) {
          if (this.activeUploads < this.maxParallelFiles) {
            this.logInfo(`: ${task.name}`);
            this.uploadFileChunks(task);
            this.activeUploads++;
          }
        }
      }
      
      // 
      if (this.activeUploads === 0) {
        const pendingTasks = this.uploadTasks.filter(task => 
          task.status === 'uploading' && task.chunkQueue.some(chunk => !chunk.uploaded)
        );
        
        if (pendingTasks.length > 0) {
          this.logInfo('');
          for (const task of pendingTasks.slice(0, this.maxParallelFiles)) {
            this.uploadFileChunks(task);
            this.activeUploads++;
          }
        }
      }
    },
    
    async uploadFileChunks(task) {
      this.logInfo(`: ${task.name}`);
      
      // 
      const activeChunks = task.chunkQueue.filter(chunk => chunk.uploading).length;
      this.logInfo(`: ${activeChunks}, : ${task.totalChunks}, : ${task.uploadedChunks}`);
      
      // 
      if (task.chunkQueue.every(chunk => chunk.uploaded)) {
        this.logInfo(` ${task.name}`);
        await this.completeFileUpload(task);
        return;
      }
      
      // 
      let startedNewUploads = 0;
      for (let i = 0; i < task.chunkQueue.length && activeChunks + startedNewUploads < this.maxParallelChunks; i++) {
        const chunk = task.chunkQueue[i];
        if (!chunk.uploaded && !chunk.uploading) {
          this.logInfo(` ${i+1}/${task.totalChunks}`);
          chunk.uploading = true;
          this.uploadChunk(task, chunk);
          startedNewUploads++;
        }
      }
      
      this.logInfo(` ${startedNewUploads} `);
    },
    
    async uploadChunk(task, chunk) {
      try {
        this.logInfo(`: =${task.name}, =${chunk.index}, =${chunk.file.size}`);
        
        //  API 
        const response = await uploadChunk(task.taskId, chunk.index, chunk.file);
        this.logInfo(`: ${JSON.stringify(response)}`);
        
        if (response.success) {
          chunk.uploaded = true;
          chunk.uploading = false;
          task.uploadedChunks++;
          
          // 
          task.progress = (task.uploadedChunks / task.totalChunks) * 100;
          this.logInfo(`: ${chunk.index}, : ${task.progress.toFixed(1)}%`);
          
          // 
          this.uploadFileChunks(task);
        } else {
          this.logWarn(`: ${chunk.index}, : ${response.message}`);
          // 
          if (chunk.retries < 3) {
            chunk.retries++;
            chunk.uploading = false;
            this.logInfo(`1 ${chunk.index} ( ${chunk.retries}/3)`);
            // 
            setTimeout(() => {
              this.uploadChunk(task, chunk);
            }, 1000);
          } else {
            chunk.uploading = false;
            task.status = 'error';
            task.error = response.message || ` ${chunk.index + 1} `;
            this.logError(` ${chunk.index} , `);
            this.checkAllTasksCompleted();
          }
        }
      } catch (error) {
        this.logError(`: ${error.message || error}`);
        // 
        if (chunk.retries < 3) {
          chunk.retries++;
          chunk.uploading = false;
          this.logInfo(`1 ${chunk.index} ( ${chunk.retries}/3)`);
          setTimeout(() => {
            this.uploadChunk(task, chunk);
          }, 1000);
        } else {
          chunk.uploading = false;
          task.status = 'error';
          task.error = ` ${chunk.index + 1} `;
          this.logError(` ${chunk.index} , `);
          this.checkAllTasksCompleted();
        }
      }
      
      // 
      this.processUploadQueue();
    },
    
    async completeFileUpload(task) {
      this.logInfo(`: ${task.name}, ID: ${task.taskId}`);
      task.status = 'verifying';
      
      try {
        this.logInfo('');
        const response = await completeChunk(task.taskId);
        this.logInfo(`: ${JSON.stringify(response)}`);
        
        if (response.success) {
          // MD5
          if (response.data.mD5Verified) {
            task.status = 'completed';
            this.logInfo(` ${task.name} `);
            
            const fileInfo = {
              fileName: response.data.originalName,
              serverFileName: response.data.serverFileName,
              size: response.data.size,
              md5: response.data.mD5,
              captureTime: response.data.captureTime || null
            };
            
            // 
            if (response.data.captureTime) {
              task.captureTime = response.data.captureTime;
              this.logInfo(` ${task.name} : ${task.captureTime}`);
            }
            
            this.logInfo(`: ${JSON.stringify(fileInfo)}`);
            this.$emit('upload-complete', fileInfo);
          } else {
            this.logError(`MD5: ${JSON.stringify(response.data)}`);
            task.status = 'error';
            task.error = `MD5: ${response.data.expectedMD5}: ${response.data.mD5}`;
          }
        } else {
          this.logError(`: ${JSON.stringify(response)}`);
          task.status = 'error';
          task.error = response.message || '';
        }
      } catch (error) {
        this.logError(`: ${error.message || error}`);
        task.status = 'error';
        task.error = '';
      }
      
      // 
      this.checkAllTasksCompleted();
      
      // 
      this.processUploadQueue();
    },
    
    checkAllTasksCompleted() {
      this.logInfo('');
      
      // 
      const isAllCompleted = this.uploadTasks.every(
        task => task.status === 'completed' || task.status === 'error'
      );
      
      this.logInfo(`: ${isAllCompleted}`);
      if (isAllCompleted && this.uploadTasks.length > 0) {
        this.logInfo('');
        this.isUploading = false;
        
        // 
        let captureTime = null;
        const taskWithCaptureTime = this.uploadTasks.find(task => 
          task.status === 'completed' && task.captureTime
        );
        
        if (taskWithCaptureTime) {
          captureTime = taskWithCaptureTime.captureTime;
          this.logInfo(`: ${taskWithCaptureTime.name}, : ${captureTime}`);
        }
        
        // 
        this.$emit('all-completed', { captureTime });
        
        // 
        const completedTasks = this.uploadTasks.filter(task => task.status === 'completed').length;
        const errorTasks = this.uploadTasks.filter(task => task.status === 'error').length;
        this.logInfo(`: ${completedTasks}, ${errorTasks}`);
      }
    }
  }
};
</script>

<style scoped>
.chunk-uploader {
  display: flex;
  flex-direction: column;
}

.upload-dialog {
  width: 100%;
  background-color: white;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
}

.upload-dialog-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  border-bottom: 1px solid #eee;
}

.upload-dialog-header h3 {
  margin: 0;
  font-size: 18px;
}

.upload-dialog-body {
  padding: 16px;
  max-height: 400px;
  overflow-y: auto;
}

.file-item {
  margin-bottom: 16px;
  padding-bottom: 16px;
  border-bottom: 1px solid #eee;
}

.file-item:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
  border-bottom: none;
}

.file-info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
  flex-wrap: wrap;
}

.file-name {
  font-weight: bold;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 50%;
}

.file-size {
  color: #666;
}

.file-status {
  width: 100%;
  margin-top: 4px;
  color: #555;
}

.error-message {
  color: #f44336;
}

.progress-container {
  height: 20px;
  background-color: #f5f5f5;
  border-radius: 4px;
  position: relative;
  overflow: hidden;
}

.progress-bar {
  height: 100%;
  background-color: #4caf50;
  transition: width 0.3s;
}

.progress-text {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #333;
  font-size: 12px;
  font-weight: bold;
}

.toggle-logs-btn {
  padding: 4px 10px;
  font-size: 12px;
  background-color: #f1f1f1;
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
  color: #333;
}

.toggle-logs-btn:hover {
  background-color: #e9e9e9;
}

.log-container {
  margin-top: 20px;
  border: 1px solid #eee;
  border-radius: 4px;
  background-color: #f9f9f9;
}

.log-header {
  padding: 8px 12px;
  border-bottom: 1px solid #eee;
  background-color: #f1f1f1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.log-header h4 {
  margin: 0;
  font-size: 14px;
  color: #333;
}

.log-actions {
  display: flex;
  gap: 8px;
}

.clear-logs-btn,
.copy-logs-btn {
  padding: 3px 8px;
  font-size: 11px;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 3px;
  cursor: pointer;
  color: #333;
}

.clear-logs-btn:hover,
.copy-logs-btn:hover {
  background-color: #f1f1f1;
}

.log-content {
  padding: 8px;
  max-height: 200px;
  overflow-y: auto;
  font-family: monospace;
  font-size: 12px;
  color: #333;
}

.log-entry {
  padding: 2px 0;
  border-bottom: 1px dotted #eee;
  white-space: pre-wrap;
  word-break: break-all;
}

.log-time {
  color: #666;
  margin-right: 6px;
}

.log-level {
  font-weight: bold;
  margin-right: 6px;
}

.log-info .log-level {
  color: #2196f3;
}

.log-warn .log-level {
  color: #ff9800;
}

.log-error .log-level {
  color: #f44336;
}

.log-message {
  word-wrap: break-word;
}

</style>
