<template>
  <div class="home">
    <!--  -->
    <header class="header">
      <div class="baby-info">
        <!--  -->
        <div class="settings">
          <button
            class="settings-btn"
            @click.stop="showThemePopup = !showThemePopup"
            aria-label=""
            title=""
          ></button>

          <div v-if="showThemePopup" class="settings-popup" @click.stop>
            <label class="popup-label"></label>
            <select v-model="currentThemeLocal" @change="onThemeChange" class="theme-select">
              <option value="light"> (light)</option>
              <option value="warm"> (warm)</option>
              <option value="beige"> (beige)</option>
              <option value="babyblue"> (babyblue)</option>
              <option value="peach"> (peach)</option>
            </select>
            <div class="popup-actions">
              <button class="btn-apply" @click="applyTheme(currentThemeLocal)"></button>
            </div>
          </div>
        </div>
         <div class="avatar"></div>
         <div class="info">
           <h1 class="baby-name"></h1>
           <p class="baby-age">{{ currentAge }}</p>
         </div>
       </div>
      <div class="stats">
        <div class="stat-item">
          <div class="stat-number">{{ totalEvents }}</div>
          <div class="stat-label"></div>
        </div>
        <div class="stat-item">
          <div class="stat-number">{{ totalPhotos }}</div>
          <div class="stat-label"></div>
        </div>
        <div class="stat-item">
          <div class="stat-number">{{ totalVideos }}</div>
          <div class="stat-label"></div>
        </div>
        <div class="stat-item">
          <div class="stat-number">{{ totalAudios }}</div>
          <div class="stat-label"></div>
        </div>
      </div>
    </header>



    <!--  -->
    <div class="timeline-container">
      <div class="timeline-header">
        <h2 class="timeline-title"></h2>
        <div class="sort-control">
          <label class="sort-checkbox">
            <input 
              type="checkbox" 
              v-model="sortAscending" 
              @change="onSortChange"
            />
            <span class="checkmark"></span>
            <span class="sort-label">{{ sortAscending ? '' : '' }}</span>
          </label>
        </div>
      </div>
      
      <!--  -->
      <div v-if="loading" class="loading-container">
        <div class="loading-spinner"></div>
        <p>...</p>
      </div>
      
      <!--  -->
      <div v-else-if="error" class="error-container">
        <div class="error-icon"></div>
        <p>{{ error }}</p>
        <button class="retry-btn" @click="loadEventsList"></button>
      </div>
      
      <!--  -->
      <div v-else class="timeline">
        <div 
          v-for="(period, index) in sortedTimelinePeriods" 
          :key="index"
          class="timeline-period"
        >
          <div class="period-header">
            <div class="period-age">{{ period.age }}</div>
          </div>
          <div class="events-grid">
            <div 
              v-for="(event, eventIndex) in period.events" 
              :key="event.id"
              :data-event-id="event.id"
              @click="viewEvent(event)"
            >
              <div v-if="shouldShowEventDate(period.events, eventIndex)" class="event-date">{{ event.date }}</div>
              
              <div class="event-card">
                <div class="event-photos">
                  <!--  -->
                  <LazyImage
                    v-for="(image, photoIndex) in getAllMediaItems(event).slice(0, 4)" 
                    :key="`${image.type}-${photoIndex}`"
                    :id="event.id"
                    :fileName="image.fileName"
                    :alt="`${event.title} - ${image.type === 'video' ? '' : ''}${photoIndex + 1}`"
                    :small="getAllMediaItems(event).length > 1"
                    :preload="shouldPreloadImage(index, photoIndex)"
                    :priority="getImagePriority(index, photoIndex)"
                    :threshold="loadingStrategy.threshold"
                    class="photo-item"
                    :class="{ 
                      'small': getAllMediaItems(event).length > 1,
                      'video-thumbnail': image.type === 'video'
                    }"
                  >
                    <template v-if="image.type === 'video'">
                      <span class="video-play-icon">
                        <svg xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 48 48">
                          <circle cx="24" cy="24" r="22" fill="#FFB6C1"/>
                          <polygon points="18,14 34,24 18,34" fill="#FFFFFF"/>
                        </svg>
                      </span>
                    </template>
                  </LazyImage>
                  <div v-if="getAllMediaItems(event).length > 4" class="more-photos">
                    +{{ getAllMediaItems(event).length - 4 }}
                  </div>
                </div>
                <div class="event-info">
                  <h3 class="event-title">{{ event.title }}</h3>
                  <p class="event-description">{{ event.description }}</p>
                  <div class="event-meta">
                    <span v-if="event.media.audios.length > 0" class="audio-indicator"></span>
                    <span v-if="event.media.videos.length > 0" class="video-indicator"></span>
                    <span class="photo-count">{{ event.media.images.length }}</span>
                    <span v-if="event.media.videos.length > 0" class="video-count">{{ event.media.videos.length }}</span>
                    <span v-if="event.media.audios.length > 0" class="audio-count">{{ event.media.audios.length }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <button class="fab" @click="addEvent">
      
    </button>

    <!--  -->
    <PerformancePanel />
  </div>
</template>

<script>
import { ref, computed, onMounted, nextTick, onUnmounted } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import dayjs from 'dayjs'
import { getEventsList, getStats, getMediaUrl } from '@/api/events'
import LazyImage from '@/components/LazyImage.vue'
import PerformancePanel from '@/components/PerformancePanel.vue'
import { preloadEventImages } from '@/utils/imageUtils'
import { adaptiveImageLoader } from '@/utils/imagePerformance'
export default {
  name: 'HomePage',
  components: {
    LazyImage,
    PerformancePanel
  },
  setup() {
    const router = useRouter()
    const route = useRoute()
    const babyBirthDate = ref(dayjs('2025-05-09')) // 202559
    
    // 
    const currentAge = computed(() => {
      const now = dayjs()
      const birth = babyBirthDate.value
      
      // ""
      if (now.isBefore(birth)) {
        return ''
      }
      
      const totalMonths = now.diff(birth, 'month')
      
      if (totalMonths < 12) {
        // 
        
        const monthStart = birth.add(totalMonths, 'month')
        const days = now.diff(monthStart, 'day')
        
        if (totalMonths === 0) {
          const totalDays = now.diff(birth, 'day')
          return `${totalDays}`
        } else {
          return `${totalMonths}${days > 0 ? ` ${days}` : ''}`
        }
      } else {
        const years = Math.floor(totalMonths / 12)
        const remainingMonths = totalMonths % 12
        return `${years}${remainingMonths > 0 ? ` ${remainingMonths}` : ''}`
      }
    })

    // localStorage
    const getSortPreference = () => {
      try {
        const saved = localStorage.getItem('baby-log-sort-ascending')
        return saved !== null ? JSON.parse(saved) : false
      } catch (error) {
        console.warn(':', error)
        return false
      }
    }

    // localStorage
    const saveSortPreference = (ascending) => {
      try {
        localStorage.setItem('baby-log-sort-ascending', JSON.stringify(ascending))
      } catch (error) {
        console.warn(':', error)
      }
    }

    // 
    const timelinePeriods = ref([])
    const loading = ref(true)
    const error = ref('')
    const sortAscending = ref(getSortPreference()) // localStorage
    const stats = ref({
      totalEvents: 0,
      totalPhotos: 0,
      totalVideos: 0,
      totalAudios: 0
    })
    const currentTheme = ref('light')
    const showThemePopup = ref(false)
    const currentThemeLocal = ref('light')

    const applyTheme = (theme) => {
      currentTheme.value = theme
      document.documentElement.setAttribute('data-theme', theme)
      localStorage.setItem('babylog-theme', theme)
      showThemePopup.value = false
    }

    // 
    const loadingStrategy = adaptiveImageLoader.getLoadingStrategy()

    // 
    const loadEventsList = async () => {
      try {
        loading.value = true
        error.value = ''
        
        const response = await getEventsList()
        if (response.success) {
          timelinePeriods.value = response.data
          
          // 
          const allEvents = response.data.flatMap(period => period.events)
          if (allEvents.length > 0 && loadingStrategy.enablePreload) {
            preloadEventImages(allEvents, loadingStrategy.preloadCount, getMediaUrl)
          }
        } else {
          error.value = response.message || ''
        }
      } catch (err) {
        error.value = ''
        console.error(':', err)
      } finally {
        loading.value = false
      }
    }

    // 
    const loadStats = async () => {
      try {
        const response = await getStats()
        if (response.success) {
          stats.value = response.data
        }
      } catch (err) {
        console.error(':', err)
      }
    }

    // APIstats
    const totalEvents = computed(() => stats.value.totalEvents)
    const totalPhotos = computed(() => stats.value.totalPhotos)
    const totalVideos = computed(() => stats.value.totalVideos)
    const totalAudios = computed(() => stats.value.totalAudios)

    // 
    const sortedTimelinePeriods = computed(() => {
      if (!timelinePeriods.value || timelinePeriods.value.length === 0) {
        return []
      }
      
      // 
      const sorted = JSON.parse(JSON.stringify(timelinePeriods.value))
      
      // 
      sorted.forEach(period => {
        if (period.events && period.events.length > 0) {
          // 
          period.events.sort((a, b) => {
            const dateA = new Date(a.date || '1970-01-01')
            const dateB = new Date(b.date || '1970-01-01')
            
            return sortAscending.value 
              ? dateA - dateB  // 
              : dateB - dateA  // 
          })
        }
      })
      
      // 
      if (sortAscending.value) {
        // 
        return sorted.sort((a, b) => {
          // period
          const dateA = new Date(a.events[0]?.date || '1970-01-01')
          const dateB = new Date(b.events[0]?.date || '1970-01-01')
          return dateA - dateB
        })
      } else {
        // 
        return sorted.sort((a, b) => {
          const dateA = new Date(a.events[0]?.date || '1970-01-01')
          const dateB = new Date(b.events[0]?.date || '1970-01-01')
          return dateB - dateA
        })
      }
    })

    // 
    const viewEvent = (event) => {
      console.log(':', event)
      // 
      router.push(`/event/${event.id}`)
    }

    const addEvent = () => {
      console.log('')
      // 
      router.push('/add')
    }
    
    const goToChunkUpload = () => {
      console.log('')
      // 
      router.push('/chunk-upload')
    }

    const onSortChange = () => {
      console.log(':', sortAscending.value ? '' : '')
      // localStorage
      saveSortPreference(sortAscending.value)
    }

    // 
    const shouldPreloadImage = (eventIndex, photoIndex) => {
      // 2
      return eventIndex < 2 && photoIndex === 0
    }

    // 
    const getImagePriority = (eventIndex, photoIndex) => {
      // 3
      if (eventIndex < 3 && photoIndex === 0) {
        return 'high'
      }
      // 6
      if (eventIndex < 6) {
        return 'normal'
      }
      // 
      return 'low'
    }

    // 
    const getAllMediaItems = (event) => {
      const allMedia = []
      
      // 
      if (event.media.images && event.media.images.length > 0) {
        event.media.images.forEach(image => {
          allMedia.push({
            ...image,
            type: 'image'
          })
        })
      }
      
      // 
      if (event.media.videos && event.media.videos.length > 0) {
        event.media.videos.forEach(video => {
          allMedia.push({
            ...video,
            type: 'video'
          })
        })
      }
      
      return allMedia
    }

    // 
    // 
    const shouldShowEventDate = (events, index) => {
      if (index === 0) return true
      
      // 
      // 
      const currentDate = events[index].date
      const prevDate = events[index - 1].date
      
      return currentDate !== prevDate
    }

    // 
    const scrollToEvent = async (eventId) => {
      await nextTick()
      
      // 
      const eventCard = document.querySelector(`[data-event-id="${eventId}"]`)
      if (eventCard) {
        // 
        eventCard.scrollIntoView({ 
          behavior: 'auto', 
          block: 'center' 
        })
      
      } else {
        console.warn(':', eventId)
      }
    }

    // ID
    const findLatestEventId = () => {
      if (timelinePeriods.value && timelinePeriods.value.length > 0) {
        // 
        const allEvents = timelinePeriods.value.flatMap(period => period.events)
        if (allEvents.length > 0) {
          const latestEvent = allEvents.sort((a, b) => new Date(b.date) - new Date(a.date))[0]
          return latestEvent.id
        }
      }
      return null
    }

    // ID
    const findClosestEventId = (targetId) => {
      if (!timelinePeriods.value || timelinePeriods.value.length === 0) {
        return null
      }

      const allEvents = timelinePeriods.value.flatMap(period => period.events)
      if (allEvents.length === 0) {
        return null
      }

      const targetIdNum = parseInt(targetId, 10)
      if (isNaN(targetIdNum)) {
        // ID
        return findLatestEventId()
      }

      // ID
      let closestEvent = allEvents[0]
      let minDifference = Math.abs(parseInt(closestEvent.id, 10) - targetIdNum)

      for (const event of allEvents) {
        const eventIdNum = parseInt(event.id, 10)
        if (!isNaN(eventIdNum)) {
          const difference = Math.abs(eventIdNum - targetIdNum)
          if (difference < minDifference) {
            minDifference = difference
            closestEvent = event
          }
        }
      }

      return closestEvent.id
    }
    // 
    const preventBack = () => {
      window.history.pushState(null, '', window.location.href)
      window.scrollTo({ top: 0, behavior: 'auto' }) 
    }
    onUnmounted(() => {
      window.removeEventListener('popstate', preventBack)
    })
    // 
    onMounted(async () => {
      window.history.pushState(null, '', window.location.href)
      window.addEventListener('popstate', preventBack)
      await Promise.all([
        loadEventsList(),
        loadStats()
      ])
      
      // 
      const highlightId = route.query.highlight
      if (highlightId) {
        // 
        const allEvents = timelinePeriods.value.flatMap(period => period.events)
        const targetEvent = allEvents.find(event => String(event.id) === String(highlightId))
        
        if (targetEvent) {
          // 
          scrollToEvent(highlightId)
        } else {
          // ID
          const closestEventId = findClosestEventId(highlightId)
          if (closestEventId) {
            scrollToEvent(closestEventId)
          }
        }
      }
    })

    return {
      currentAge,
      timelinePeriods,
      sortedTimelinePeriods,
      sortAscending,
      totalEvents,
      totalPhotos,
      totalVideos,
      totalAudios,
      loading,
      error,
      viewEvent,
      addEvent,
      goToChunkUpload,
      loadEventsList,
      onSortChange,
      getSortPreference,
      saveSortPreference,
      getMediaUrl,
      scrollToEvent,
      findLatestEventId,
      findClosestEventId,
      shouldPreloadImage,
      getImagePriority,
      getAllMediaItems,
      shouldShowEventDate,
      loadingStrategy,
      showThemePopup,
      currentThemeLocal,
      applyTheme
    }
  }
}
</script>

