}

/* ...existing code... */



/*  */
.timeline-container {
  max-width: 800px;
  margin: 0 auto;
}

.timeline-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.timeline-title {
  font-size: 24px;
  font-weight: 600;
  color: #2c3e50;
  margin: 0;
}

.sort-control {
  display: flex;
  align-items: center;
}

.sort-checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 14px;
  color: #7f8c8d;
  user-select: none;
}

.sort-checkbox input[type="checkbox"] {
  display: none;
}

